import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { selectIsCountryFetching } from '../../redux/country/country.selector';
import CircularIndeterminate from '../spinner-indeterminate/spinner-indeterminate.component';


const useStyles = makeStyles({
  root: {
    maxWidth: 345,
  },
  media: {
    height: 140,
    width: 345
  },
});

function MediaCard({ CountryName,Capital,Population,LatLong,CountryFlag,handelClick,isFetching }) {
    // console.log('In Media card - ',CountryName,Capital,Population,LatLong,CountryFlag);
  const classes = useStyles();

  return (
      (isFetching)? <CircularIndeterminate />
      :
      ((CountryName !== undefined) ? (
        <Card className={classes.root}>
            <CardActionArea>
                <CardMedia
                className={classes.media}
                image={CountryFlag}
                title="Country Flag"
                />
                <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                    {CountryName}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`Capital - ${Capital}`}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`Population - ${Population}`}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`Latitude / Longitude - ${LatLong[0]},${LatLong[1]}`}
                </Typography>
                </CardContent>
            </CardActionArea>
            <CardActions>
                <Button size="small" color="primary" onClick={handelClick}>
                See Weather
                </Button>
            </CardActions>
            </Card>
      )
      : <div className='No-Item-Container'>Search for a country...</div>)
    
  );
}

const mapStateToProps = createStructuredSelector({
  isFetching: selectIsCountryFetching
});

 export default connect(mapStateToProps)(MediaCard);
