import React, { useEffect } from 'react';
import { withStyles,makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';

import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { fetchWeatherStart } from '../../redux/country/country.action';
import { selectWeatherValues,selectIsWeatherFetching } from '../../redux/country/country.selector';

import CircularIndeterminate from '../spinner-indeterminate/spinner-indeterminate.component';

const useStyles = makeStyles({
  paperWidthSm: {
    minWidth: 230,
    width: 230
  },
});

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const imageStyles = {
  padding: '70px'
}

// const spinnerStyle = {
//   display: 'flex',
//   justifyContent: 'center',
//   alignItems: 'center',
//   alignContent: 'center'
// }

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export function CustomizedDialogs({ open,handleClose,fetchWeatherStart,weatherValues,isFetching }) {
    const classes = useStyles();

    const { Temperature,WeatherIcons,WindSpeed,Precip } = weatherValues;
  
    // console.log('Weathervalues - ',Temperature,WeatherIcons,WindSpeed,Precip);

    useEffect(() => {
        fetchWeatherStart();
    },[fetchWeatherStart]);

  return (
    <div>
      <Dialog classes={{paperWidthSm: classes.paperWidthSm}} onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          Current Weather
        </DialogTitle>
        {/* <div className='spinner-container' style={spinnerStyle}>
          <CircularIndeterminate /> 
        </div> */}
        {(isFetching) ?        
          <CircularIndeterminate /> 
        : (<div>
            <img style={imageStyles} src={WeatherIcons} alt='item' />
            <DialogContent dividers>
              <Typography gutterBottom>
                Temperature - {Temperature}
              </Typography>
              <Typography gutterBottom>
                WindSpeed - {WindSpeed}
              </Typography>
              <Typography gutterBottom>
              Precip - {Precip}
              </Typography>
            </DialogContent>
         </div>)}
        <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

const mapStateToProps = createStructuredSelector({
    weatherValues: selectWeatherValues,
    isFetching: selectIsWeatherFetching
})

const mapDispatchToProps = dispatch => ({
    fetchWeatherStart: () => dispatch(fetchWeatherStart())
})

export default connect(mapStateToProps,mapDispatchToProps)(CustomizedDialogs);