import { createSelector } from 'reselect';

const selectCountry = state => state.country;

export const selectCountryValues = createSelector(
    [selectCountry],
    country => country.countryInfo
);

export const selectWeatherValues = createSelector(
    [selectCountry],
    country => country.countryWeather
);

export const selectIsCountryFetching = createSelector(
    [selectCountry],
    country => country.isCountryFetching
);

export const selectIsWeatherFetching = createSelector(
    [selectCountry],
    country => country.isWeatherFetching
);